import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { variable } from '../global/url';


export default function CategoryTable() {

    const [category, setCategory] = useState([])
    let num = 1

    useEffect(() => {
        axios({
            method: "GET",
            url: variable.MainUrl + "api/v1/category/",
        }).then(response => {
            setCategory(response.data)
        })
    }, [])

    return(
        <div className="container">
        <h3 className="text-center">All Category</h3>
        <Table striped bordered hover variant="light" className="text-center">
          <thead>
            <tr>
              <th>№</th>
              <th>Title</th>
              <th>Description</th>
              <th>Options</th>
            </tr>
          </thead>
          <tbody>
            {category.map(cat => (
            <tr key={cat.id}>
              <td className="col-1">{num++}</td>
              <td className="col-3 text-start"><Link to={{ pathname: `/category_detail/${cat.id}`, fromDashboard: false }} className="ms-2">{cat.category_title}</Link></td>
              <td className="col-6">{cat.category_description}</td>
              <td className="col-2">
                <div className="d-flex justify-content-around">
                    <div>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16">
                          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                          <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                        </svg>
                    </div>
                    <div>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-eraser" viewBox="0 0 16 16">
                          <path d="M8.086 2.207a2 2 0 0 1 2.828 0l3.879 3.879a2 2 0 0 1 0 2.828l-5.5 5.5A2 2 0 0 1 7.879 15H5.12a2 2 0 0 1-1.414-.586l-2.5-2.5a2 2 0 0 1 0-2.828l6.879-6.879zm2.121.707a1 1 0 0 0-1.414 0L4.16 7.547l5.293 5.293 4.633-4.633a1 1 0 0 0 0-1.414l-3.879-3.879zM8.746 13.547 3.453 8.254 1.914 9.793a1 1 0 0 0 0 1.414l2.5 2.5a1 1 0 0 0 .707.293H7.88a1 1 0 0 0 .707-.293l.16-.16z"/>
                        </svg>
                    </div>
                </div>
              </td>
            </tr>
            ))}
          </tbody>
        </Table>
            <br/>
            <div className="text-center">
                <Link to={{ pathname: '/category_add', fromDashboard: false }}><button className="btn btn-success">Add Category</button></Link>
            </div>
        </div>
    )
}
